<template>
  <div class="sidebar">
    <!--background-color="#324157"    text-color="#bfcbd9" active-text-color="#20a0ff" -->
    <el-menu class="sidebar-el-menu" :default-active="onRoutes" :collapse="collapse"  router background-color="#d0f8ce" text-color="#607d8b" active-text-color="#ff6d00">
      <template v-for="item in items">
        <!-- 如果有多级菜单 -->
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index">
            <template slot="title">
              <icon :name='item.icon' :w="25" :h="25"></icon><span slot="title" style="margin-left:25px">{{ item.title }}</span>
              <hr>
              <!-- <i :class="item.icon"></i><span slot="title">{{ item.title }}</span> -->
            </template>

            <template v-for="subItem in item.subs">
              <!-- 处理三级菜单 -->
              <el-submenu v-if="subItem.subs" :index="subItem.index" :key="subItem.index">
                <template slot="title">{{ subItem.title }}</template>
                <!-- <template slot="title">
                  <icon :name='subItem.icon' :w="25" :h="25"></icon><span slot="title" style="margin-left:15px">{{ subItem.title }}</span>
                </template> -->
                <el-menu-item v-for="(threeItem,i) in subItem.subs" :key="i" :index="threeItem.index">
                  {{ threeItem.title }}
                </el-menu-item>
              </el-submenu>
              <!-- 没有三级菜单 -->
              <el-menu-item v-else :index="subItem.index" :key="subItem.index">
                <icon :name='subItem.icon' :w="20" :h="20"></icon><span style="margin-left:25px">{{ subItem.title }}</span>
                <!-- {{ subItem.title }} -->
              </el-menu-item>

            </template>

          </el-submenu>
        </template>
        <!-- 没有多级菜单 -->
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <!-- <i :class="item.icon"></i><span slot="title">{{ item.title }}</span> -->
            <icon :name='item.icon' :w="25" :h="25"></icon><span slot="title" style="margin-left:25px">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

