          title: '系统首页'
        },
        {
          icon: 'dingdantongji',
          index: '1',
          title: '前台管理',
          subs: [
            {
              icon: 'duohuanshagncheng',
              index: 'entry',
              title: '会员入场'
            },
            {
              icon: 'fenzhangxitong',
              index: 'BaseTable',
              title: '会员协议'
            },
            {
              icon: 'fenzutongji',
              index: '13',
              title: '水吧收银'
            },
            {
              icon: 'huiyantongji',
              index: '14',
              title: '会员充值'
            },
            {
              icon: 'huiyuanguanli',
              index: '15',
              title: '储物柜出租'
            },
            {
              icon: 'huiyuanjifen',
              index: '16',
              title: '手牌查询'
            },
            {
              icon: 'huiyuanka',
              index: '17',
              title: '私教购买'
            },
            {
              icon: 'huiyuanliuliuliang',
              index: '18',
              title: '私教签课'
