              title: '将到期会员'
            },
            {
              icon: 'shebieguanli',
              index: '28',
              title: '已过期会员'
            },
            {
              icon: 'shensuchuli',
              index: '29',
              title: '会员投诉'
            },
            {
              icon: 'wuliaoxiazai',
              index: '30',
              title: '会员生日'
            },
            {
              icon: 'xiaoxitongzhi',
              index: '31',
              title: '会员健身记录查询'
            },
            {
              icon: 'wuliaoxiazai',
              index: '32',
              title: '会员批量延期'
            }
          ]
        },
        {
          icon: 'xiaoxitongzhi',
          index: '4',
          title: '教练管理',
          subs: [
            {
              icon: 'yingshoubaobiao',
