            {
              icon: 'youhuijuan',
              index: '41',
              title: '私教推广跟进'
            },
            {
              icon: 'dingdantongji',
              index: '42',
              title: '会员分配'
            },
            {
              icon: 'duohuanshagncheng',
              index: '43',
              title: '工作计划'
            },
            {
              icon: 'fenzhangxitong',
