              icon: 'fenzutongji',
              index: '45',
              title: '私教课程余次明细'
            },
            {
              icon: 'huiyantongji',
              index: '46',
              title: '私教签课明细表'
            },
            {
              icon: 'huiyuanguanli',
              index: '47',
              title: '批改分配'
            }
          ]
        },
        {
          icon: 'huiyuanjifen',
          index: '5',
          title: '销售管理',
          subs: [
            {
              icon: 'huiyuanka',
              index: '51',
              title: '现有会员'
            },
            {
              icon: 'huiyuanliuliuliang',
              index: '52',
