            {
              icon: 'mendianguanli',
              index: '56',
              title: '批改分配'
            }
          ]
        },
        {
          icon: 'mendiantongji',
          index: '6',
          title: 'APP管理',
          subs: [
            {
              icon: 'mendianzhuanshi',
              index: '61',
              title: '课程表信息'
            }
          ]
        },
        {
          icon: 'shangjialiebiao',
          index: '7',
          title: '会所管理',
          subs: [
            {
              icon: 'shangpinguanli',
              index: 'company',
              title: '公司设置'
            },
            {
              icon: 'shebeipaibi',
              index: 'store',
              title: '门店设置'
            },
            {
              icon: 'shebeitongji',
              index: 'cards',
              title: '卡种设置'
            },
            {
              icon: 'shebiebangding',
              index: '74',
              title: '人事档案'
            },
            {
              icon: 'shebieguanli',
              index: '75',
              title: '权限分配'
            },
            {
              icon: 'shensuchuli',
              index: '76',
              title: '私教卡设置'
            },
            {
              icon: 'wuliaoxiazai',
              index: '77',
              title: '商品信息设置'
            },
            {
              icon: 'xiaoxitongzhi',
              index: '78',
              title: '配套项目设置'
            },
            {
              icon: 'yingshoubaobiao',
              index: '79',
              title: '储物柜设置'
            }
          ]
        },
        {
          icon: 'youhuijuan',
          index: '8',
          title: '报表管理'
        }
      ]
    }
  },
  computed: {
    onRoutes () {
      return this.$route.path.replace('/', '')
    }
  },
  created () {
    // 通过 Event Bus 进行组件间通信，来折叠侧边栏
    bus.$on('collapse', msg => {
      this.collapse = msg
    })
  }
}
</script>

<style scoped>
.sidebar {
  display: block;
  position: absolute;
  left: 0;
  top: 70px;
  bottom: 0;
  overflow-y: scroll;
}
.sidebar::-webkit-scrollbar {
  width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
  width: 250px;
}
.sidebar > ul {
  height: 100%;
}
</style>



