import Cookies from 'js-cookie'
import { Message } from 'element-ui'
import _this from '../main.js'

const CompanyKey = 'CompanyId'
export function getTokenCompanyKey () {
  console.log(Cookies.get(CompanyKey))
  if (Cookies.get(CompanyKey) == null) {
    Message({
      showClose: true,
      message: '公司的id没有',
      type: 'error',
      onClose: (message) => {
        _this.$router.push('/login')
        console.log(message)
      }
    })
  }
  return Cookies.get(CompanyKey)
}

export function setTokenCompanyKey (CompanyId) {
  Message({
    showClose: true,
    message: '设置公司的id',
    type: 'info',
    onClose: (message) => {
      console.log(message)
    }
  })
  return Cookies.set(CompanyKey, CompanyId)
}

export function removeTokenCompanyKey () {
  Message({
    showClose: true,
    message: '移除公司的id',
    type: 'warning',
    onClose: (message) => {
      console.log(message)
    }
  })
  return Cookies.remove(CompanyKey)
}



