import Cookies from 'js-cookie'
import { Message } from 'element-ui'
import _this from '../main.js'

const StoreKey = 'StoreId'

export function getTokenStoreKey () {
  console.log(Cookies.get(StoreKey))
  if (Cookies.get(StoreKey) == null) {
    Message({
      showClose: true,
      message: '门店的id没有',
      type: 'error',
      onClose: (message) => {
        _this.$router.push('/login')
        console.log(message)
      }
    })
  }
  return Cookies.get(StoreKey)
}

export function setTokenStoreKey (StoreId) {
  Message({
    showClose: true,
    message: '设置门店的id',
    type: 'info',
    onClose: (message) => {
      console.log(message)
    }
  })
  return Cookies.set(StoreKey, StoreId)
}

export function removeTokenStoreKey () {
  Message({
    showClose: true,
    message: '移除门店的id',
    type: 'warning',
    onClose: (message) => {
      console.log(message)
    }
  })
  return Cookies.remove(StoreKey)
}



